<template>
    <div class="water-container">
        <div class="border-title"><span>▎</span>{{ title }}</div>
        <div class="water-border">
            <div class="solid-border">
                <CenterChart :id="title" :tips="number" :colorObj="colorObj" />
            </div>
        </div>
    </div>
</template>

<script>
import CenterChart from '@/components/echart/center/centerChartRate'

export default {
    components: {
        CenterChart,
    },
    props: {
        number: {
            type: Number,
            default: 45,
        },
        title: {
            type: String,
            default: '标题',
        },
    },
    watch: {
        number: {
            handler(newData) {
                let normal = {
                    textStyle: '#A8D6EB',
                    series: {
                        color: ['#1E81BC', 'transparent'],
                        dataColor: {
                            normal: '#06DBB2',
                            shadowColor: '#97e2f5',
                        },
                    },
                }
                let red = {
                    textStyle: '#D80604',
                    series: {
                        color: ['#1E81BC', 'transparent'],
                        dataColor: {
                            normal: '#D80604',
                            shadowColor: '#D80604',
                        },
                    },
                }
                this.colorObj = newData > 70 ? red : normal
            },
            immediate: true,
            deep: true,
        },
    },
    data() {
        return {
            colorObj: {},
        }
    },
}
</script>

<style lang="scss" scoped>
