    background-size: 100% 100%;
    padding: 8px 10px;
    margin: 5px;
    span {
        color: #f6c35d;
    }
}

@keyframes turn-animation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes return-animation {
    0% {
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
.water-border {
    margin: 30px;
    border: 3px dashed #104b75;
    animation: turn-animation 10s linear 0s infinite normal;
    border-radius: 50%;
    width: 110px;
    height: 110px;
    display: flex;
    justify-content: center;
    align-items: center;
    .solid-border {
        animation: return-animation 10s linear 0s infinite normal;
        border: 3px solid #104b75;
        border-radius: 50%;
        width: 100px;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
