<template>
    <div id="cockpit">
        <div class="cockpit-container">
            <!-- 决策分析 -->
            <div class="cockpit-container-left item">
                <CloudTitle style="margin: 20px 0 10px;" title="业财支撑" />
                <div class="btn-container">
                    <img src="../../assets/image/cockpit/btn1.png" />
                    <!-- 供应商要素 -->
                    <router-link to="/box/supplier">
                        <img src="../../assets/image/cockpit/btn2.png" />
                    </router-link>
                    <img src="../../assets/image/cockpit/btn3.png" />
                    <img src="../../assets/image/cockpit/btn4.png" />
                    <img src="../../assets/image/cockpit/btn5.png" />
                </div>
            </div>
            <!-- 地图 -->
            <div class="cockpit-container-center item">
                <CenterMap v-if="!id" style="height: 605px;"></CenterMap>
                <CenterMap410 v-else style="height: 605px;"></CenterMap410>
            </div>
            <!-- 运营监控 -->
            <div class="cockpit-container-right item">
                <CloudTitle style="margin: 20px 0 10px;" title="预警监测" />
                <div class="cell-container">
                    <img
                        style="width: 100%; height: 15px;"
                        src="../../assets/image/cockpit/right-top-bg.png"
                        alt=""
                    />
                    <div class="content">
                        <WaterRing
                            v-for="(item, index) in ring"
                            :key="index"
                            :title="item.title"
                            :number="item.number"
                        />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import CloudTitle from '@/components/cloud-title/index.vue'
import CenterMap from '@/components/echart/cockpit/map'
import CenterMap410 from '@/components/echart/cockpit/map410'
import WaterRing from '@/components/echart/cockpit/water-ring'
export default {
    data() {
        return {
            id: null,
            ring: [
                {
                    title: '对公付款无合同',
                    number: 30,
                },
                {
                    title: '合同付款单位不匹配',
                    number: 35,
                },
                {
                    title: '超合同金额付款',
                    number: 50,
                },
                {
                    title: '无预算付款',
                    number: 85,
                },
                {
                    title: '收款单位名目不匹配',
                    number: 30,
                },
                {
                    title: '超预算付款',
                    number: 60,
                },
            ],
        }
    },
    components: {
        CloudTitle,
        WaterRing,
        CenterMap,
        CenterMap410,
    },
    mounted() {
        if (this.$route.query.id == 410) {
            // 辽宁沈阳410厂
            this.id = this.$route.query.id
        }
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 6) - 1
                let tmp2 = Math.ceil(Math.random() * 2) - 1
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let ring = JSON.parse(JSON.stringify(this.ring))
                    let num = ring[tmp1].number
                    if (num <= 25) {
                        tmp2 = 0
                    }
                    if (num >= 90) {
                        tmp2 = 1
                    }
                    ring[tmp1].number = tmp2 == 0 ? num + 5 : num - 5
                    this.ring = ring
                }
            }, 2000)
        },
    },
}
</script>

