<script>
// import Chart from './chart.vue'
import CloudTitle from '@/components/cloud-title/index.vue'

export default {
    data() {
        return {
            vdata: [
                {
                    title: '账户管控',
                    value: 3002,
                    unit: '个',
                },
                {
                    title: '资金集中',
                    value: 300922,
                    unit: '万元',
                },
                {
                    title: '资金预算',
                    value: 10102,
                    unit: '万元',
                },
                {
                    title: '债务融资',
                    value: 111222,
                    unit: '万元',
                },
                {
                    title: '资金结算',
                    value: 111220,
                    unit: '万元',
                },
                {
                    title: '票据管理',
                    value: 101,
                    unit: '条',
                },
            ],
            cdata: [
                {
                    name: '沈阳410厂、606所',
                    value: 9,
                },
            ],
        }
    },
    computed: {
        config() {
            return item => {
                return {
                    number: [item.value],
                    content: '{nt}',
                    textAlign: 'center',
                    style: {
                        fill: '#c0c2d2',
                        fontSize: '20',
                    },
                }
            }
        },
    },
    components: {
        CloudTitle
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 6) - 1
                let tmp2 = Math.ceil(Math.random() * 20) - 1
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let vdata = JSON.parse(JSON.stringify(this.vdata))
                    let num = vdata[tmp1].value
                    num = num + tmp2
                    vdata[tmp1].value = num
                    this.vdata = vdata
                }
            }, 3000)
        },
    },
}
</script>

